/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header { width: 100%; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; z-index: 999; transition: all 0.2s linear; }

.header { padding: 42px 0; }

.header-wrapper { max-width: 1340px; margin: auto; display: flex; justify-content: space-between; align-items: center; }

.header-content { display: flex; justify-content: space-between; }

.header-nav ul li { display: inline-block; margin-right: 50px; }

.header-logo { margin-left: -40px; }

.header-lang { cursor: pointer; position: relative; width: 25px; }

.header-lang__title { font-weight: bold; position: relative; }

.header-lang__title:after { position: absolute; right: -15px; top: 3px; content: ''; border: solid #272727; border-width: 0 3px 3px 0; display: inline-block; padding: 3px; transform: rotate(45deg); }

.header-lang__title.active:after { top: 7px; transform: rotate(-136deg); }

.header-lang .list-lang { display: none; position: absolute; top: 38px !important; width: 75px !important; margin-left: -18px !important; background-color: #fff; text-align: center; padding: 10px 0 6px 0; border-radius: 2px; line-height: 32px; min-width: 75px; max-height: 850px; box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); }

.header-lang .list-lang li { margin-bottom: 0; }

.header-lang .list-lang:before { position: absolute; top: -8px; content: " "; border-left: 8px dashed transparent; border-right: 8px dashed transparent; border-top: 0; left: 50%; transform: translate(-50%, 0); border-bottom: 8px solid #fff; }

.header .toggle-menu { display: none; text-align: center; line-height: 40px; z-index: 1; height: 20px; width: 20px; position: absolute; right: 10px; top: 40px; z-index: 999; }

.header .toggle-menu span { position: relative; display: block; }

.header .toggle-menu span, .header .toggle-menu span:after, .header .toggle-menu span:before { width: 100%; height: 3px; background-color: #151515; transition: all .3s; backface-visibility: hidden; border-radius: 2px; }

.header .toggle-menu span:before { background-color: #151515; }

.header .toggle-menu span:after, .header .toggle-menu span:before { content: ""; position: absolute; left: 0; top: 3.75pt; }

.header .toggle-menu span:after { top: 7.5pt; }

.header .toggle-menu.on span { background-color: transparent; }

.header .toggle-menu.on span:before { transform: rotate(45deg) translate(-2px, 0px); top: 3pt; }

.header .toggle-menu.on span:after { transform: rotate(-45deg) translate(3px, -5px); top: 6pt; }

@media screen and (max-width: 1400px) { .header-logo { margin-left: 0; }
  .header-wrapper, .footer-wrapper { padding: 0 30px; } }

@media screen and (max-width: 767px) { .header-wrapper, .footer-wrapper { padding: 0 10px; }
  .header-lang { margin-right: 55px; }
  .header { padding: 25px 0; }
  .header .toggle-menu { display: block; }
  .header-nav { opacity: 0; visibility: hidden; position: fixed; left: 0; top: 0; width: 100%; height: 100vh; background-color: #fff; z-index: 999; margin-top: 0; text-align: center; justify-content: center; align-items: center; transition: all 0.2s linear; flex-direction: column; }
  .header-nav.opened { display: flex; opacity: 1; visibility: visible; }
  .header-nav ul li { display: block; margin: 30px 0 !important; }
  .header-nav ul li a { font-weight: 600; opacity: 1; } }

