================================================================================
*/
footer { text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

.footer-wrapper { max-width: 1410px; margin: auto; display: flex; justify-content: space-between; align-items: center; }

.footer .header-nav ul li { display: inline-block; margin-right: 48px; }

.footer .header-nav ul li:last-of-type { margin-right: 0; }

.footer .header-logo { margin-left: 0; }

.footer-bottom { padding: 30px 0; opacity: 0.5; }

@media screen and (max-width: 767px) { .footer-wrapper { flex-direction: column; }
