.icon-load { background: url("../img/loader.gif") no-repeat left top; width: 40px; height: 40px; position: fixed; left: 50%; top: 50%; margin-left: -20px; margin-left: -20px; display: none; }

.loaded .icon-load { display: block; }

/* btn */
.btn { display: inline-block; width: 100%; max-width: 220px; line-height: normal; font-size: 14px; text-align: center; color: #1f1f1f; padding: 15px 30px; border: 2px solid #FFDE00; background-color: #fff; border-radius: 120px; position: relative; overflow: hidden; white-space: nowrap; transition: all 0.2s linear; font-weight: 600; }

.btn:hover { background-color: #FFDE00; }

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; }

.about { padding-top: 68px; }

.about-content { display: flex; align-items: center; justify-content: space-between; }

.about-info, .about-img { width: 50%; }

.about-info { margin-top: -11px; }

.about-info p { margin-top: 50px; opacity: 0.8; width: 91%; line-height: 200%; }

.about-img { position: relative; width: 40%; }

.about-img img { display: block; margin: auto; margin-right: 80px; }

.about-img__coin1, .about-img__coin2 { position: absolute; }

.about-img__coin1 { left: -27px; top: 19px; }

.about-img__coin2 { right: -82px; bottom: 46px; }

.about-line { position: absolute; left: 51.5%; transform: translate(-50%); top: 0; opacity: .5; }

.about-line2 { position: absolute; left: 0; top: 300px; }

.about-line3 { position: absolute; right: 0; top: -10px; }

.features { margin-top: 114px; position: relative; }

.features h3 { max-width: 607px; }

.features-items { display: flex; justify-content: space-between; margin-top: 63px; flex-wrap: wrap; padding-bottom: 100px; }

.features-items__col { max-width: 555px; width: calc(50% - 22px); }

.features-item { background: #FFFFFF; border: 1px solid rgba(194, 194, 194, 0.2); box-shadow: 0px 10px 20px rgba(58, 76, 100, 0.1); display: flex; justify-content: flex-start; align-items: center; width: 100%; padding: 32px 36px; padding-right: 0; align-self: flex-start; margin-bottom: 30px; }

.features-item__icon { width: 26px; height: 26px; background: linear-gradient(180deg, #2261EF 0%, #017EFE 100%); border-radius: 50%; display: flex; justify-content: center; align-items: center; position: relative; }

.features-item__icon span { font-size: 11px; }

.features-item__icon:before { position: absolute; left: -15px; top: -15px; content: ''; opacity: 0.5; border: 10px solid rgba(34, 97, 239, 0.3); width: 56px; height: 56px; border-radius: 50%; }

.features-item p { margin-left: 32px; width: 86%; line-height: 31px; }

.features-line { position: absolute; top: -42%; left: -7px; z-index: -1; width: 100%; }

.features-line img { width: 100%; }

.features-line2 { position: absolute; right: 0; bottom: 0; }

.team { background: linear-gradient(180deg, #2261EF 0%, #017EFE 100%); padding-top: 106px; padding-bottom: 178px; position: relative; }

.team h2 { z-index: 9; position: relative; }

.team:after, .team:before { position: absolute; left: 50%; transform: translate(-50%); top: 0; content: ''; background-image: url(../img/main/team-map.svg); width: 100%; height: 100%; background-repeat: no-repeat; background-position: center; background-size: contain; opacity: .1; }

.team:before { background-image: url(../img/main/team-bg.svg); }

.team-content { margin-top: 85px; display: flex; min-height: 355px; z-index: 99; position: relative; justify-content: space-between; }

.team-item { max-width: 255px; text-align: center; color: #ffffff; }

.team-item__img { width: 134px; height: 134px; background: rgba(255, 255, 255, 0.2); border-radius: 50%; padding: 15px; margin: auto; margin-bottom: 35px; }

.team-item__img-wrap { overflow: hidden; width: 117px; height: 117px; border-radius: 50%; margin: -6px; }

.team-item__img-wrap img { transform: scale(1.04); }

.team-item__name { font-weight: bold; font-size: 18px; line-height: 30px; text-transform: uppercase; }

.team-item__prof { font-size: 15px; line-height: 25px; opacity: 0.8; display: block; margin-bottom: 12px; margin-top: 2px; }

.team-item__mail { font-weight: bold; font-size: 15px; line-height: 30px; }

.team-item:nth-child(even) { align-self: flex-end; }

.map { padding-top: 109px; padding-bottom: 190px; position: relative; }

.map-bg { position: absolute; right: 0; top: 30%; content: ''; width: 703px; height: 715px; }

.map h2 { color: #272727; }

.map-items { margin-top: 73px; padding-left: 105px; z-index: 9; position: relative; }

.map-items:before { position: absolute; top: 0; bottom: 0; left: 10px; margin: auto; content: ''; height: 87.7%; background-color: #2261EF; width: 1px; opacity: 0.3; }

.map-item { max-width: 795px; width: 100%; background: #FFFFFF; border: 1px solid rgba(194, 194, 194, 0.2); box-shadow: 0px 10px 20px rgba(58, 76, 100, 0.1); position: relative; display: flex; padding: 35px 55px; align-items: center; min-height: 149px; margin-bottom: 28px; }

.map-item:before { position: absolute; left: -95px; top: 51%; content: ''; width: 40px; height: 1px; background-color: #2261EF; opacity: 0.3; }

.map-item__img { width: 80px; text-align: center; }

.map-item__img img { display: block; margin: auto; }

.map-item__text { margin-left: 35px; }

.map-item__text span { font-weight: bold; font-size: 18px; line-height: 30px; text-transform: uppercase; }

.map-item__text p { font-size: 16px; line-height: 27px; margin-top: 5px; }

.map-item__icon { width: 16px; height: 16px; background: linear-gradient(180deg, #2261EF 0%, #017EFE 100%); border-radius: 50%; position: absolute; left: -9px; top: 46%; }

.map-item__icon span { font-size: 11px; }

.map-item__icon:before { position: absolute; left: -17px; top: -17px; content: ''; opacity: 0.5; border: 10px solid rgba(34, 97, 239, 0.3); width: 50px; height: 50px; border-radius: 50%; }

.map-line { position: absolute; right: 0; top: 0; }

.map-line2 { position: absolute; left: 0; top: 20%; }

.map-line3 { position: absolute; left: 0; bottom: -10%; width: 100%; z-index: -1; }

.map-line3 img { width: 100%; }

@media screen and (max-width: 1200px) { .team-item { max-width: 190px; }
  .team-content { min-height: 400px; } }

@media screen and (max-width: 991px) { h1, h2 { font-size: 60px; }
  .about-img__coin2 { right: -190px; }
  .header-logo { width: 160px; }
  .header-logo img { width: 100%; }
  .header-nav ul li { margin-right: 20px; }
  .header-nav ul li a { font-size: 14px; }
  .header-lang { font-size: 14px; }
  .features-item { padding: 20px 25px; }
  .features-item p { font-size: 14px; width: 80%; }
  .team-content { flex-wrap: wrap; justify-content: space-around; }
  .team-item:nth-child(even) { align-self: flex-start; }
  .team-item { max-width: 100%; width: 45%; margin-bottom: 30px; }
  .team { padding-bottom: 76px; }
  .map-line2 { display: none; }
  .map-bg { right: -55%; }
  .footer-bottom { font-size: 14px; }
  .about-img { width: 42%; }
  .about-img__coin2 { right: -120px; bottom: -20px; }
  .about-img img { max-width: 250px; }
  .about-img__coin1 { left: -85px; } }

@media screen and (max-width: 767px) { .about-content { flex-wrap: wrap; }
  .about-info, .about-img { width: 100%; text-align: center; }
  h1, h2 { font-size: 45px; text-align: center; }
  .wrapper { padding: 0 20px; }
  .about { padding-top: 60px; }
  .about-info p { width: 100%; margin: 30px 0; }
  .about-line2 { display: none; }
  .about-img img { width: 100%; max-width: 270px; margin: auto; }
  .about-img img.about-img__coin1 { max-width: 110px; left: -50px; top: -10px; }
  .about-img img.about-img__coin2 { max-width: 155px; right: -55px; bottom: -5px; }
  .features { margin-top: 55px; }
  h3 { text-align: center; font-size: 27px; }
  .features-items { flex-wrap: wrap; margin-top: 40px; padding-bottom: 40px; z-index: 9; position: relative; }
  .features-items__col { width: 100%; }
  .features-item { flex-direction: column; justify-content: center; padding: 30px; padding-bottom: 15px; }
  .features-item p { margin: auto; width: 100%; text-align: center; margin-top: 25px; }
  .team { padding: 55px 0; padding-bottom: 20px; }
  .team-content { margin-top: 45px; }
  .team-item { width: 100%; }
  .team-item__img { margin-bottom: 15px; }
  .team-item__prof { margin-bottom: 2px; }
  .team:after, .team:before { display: none; }
  .map { padding: 50px 0; }
  .map-items { padding-left: 0; }
  .map-items:before { display: none; }
  .map-item { flex-direction: column; text-align: center; padding: 20px; width: 95%; margin: auto; margin-bottom: 20px; }
  .map-item__text { margin-left: 0; margin-top: 20px; }
  .map-bg { display: none; }
  .features-items__col { max-width: 100%; }
  .animated { /*CSS transitions*/ -o-transition-property: none !important; -moz-transition-property: none !important; -ms-transition-property: none !important; -webkit-transition-property: none !important; transition-property: none !important; /*CSS animations*/ -webkit-animation: none !important; -moz-animation: none !important; -o-animation: none !important; -ms-animation: none !important; animation: none !important; }
  .header-lang .list-lang { width: 38px; background-color: #fff; text-align: center; padding-top: 5px; }
  .about-img { max-width: 350px; margin: auto; }
  .about-img img { max-width: 165px; }
  .about-img img.about-img__coin1 { max-width: 75px; left: 0px; }
  .about-img img.about-img__coin2 { max-width: 120px; right: -15px; } }
